// Fichero src/services/localStorage.js

// Funcin que obtiene una propiedad del local storage
// Si esta propiedad no existe porque es la primera vez que la usuaria entra en la pgina la funcin devuelve el valor de defaultValue
// Que esta funcin devuelva un valor por defecto es una cmoda manera de trabajar, as esta comprobacin no la tenemos que hacer App.js
const get = (key, defaultValue) => {
    const localStorageData = localStorage.getItem(key);
    if (localStorageData === null) {
      return defaultValue;
    } else {
      return JSON.parse(localStorageData);
    }
  };
  
  // Funcin que guarda una propiedad y su valor en el local storage
  const set = (key, value) => {
    const localStorageData = JSON.stringify(value);
    localStorage.setItem(key, localStorageData);
  };
  
  // Funcin que borra una propiedad del local storage
  const remove = key => {
    localStorage.removeItem(key);
  };
  
  // Funcin que limpia todo el local storage
  const clear = () => {
    localStorage.clear();
  };
  
  // Creamos un objeto temporal que es el que queremos exportar
  // Este objeto tiene la una propiedad get cuyo valor es la funcin get
  // Este objeto tiene la una propiedad set cuyo valor es la funcin set
  // Este objeto tiene la una propiedad remove cuyo valor es la funcin remove
  // Este objeto tiene la una propiedad clear cuyo valor es la funcin clear
  const objectToExport = {
    get: get,
    set: set,
    remove: remove,
    clear: clear
  };
  
  // Exportamos el objeto para que pueda ser usado desde App
  export default objectToExport;